@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@800&family=Josefin+Sans:wght@100&family=Nova+Round&display=swap');

$font-1: 'Nova Round' , cursive;
$font-2: 'Dosis' , sans-serif;
$font-3: 'Josefin sans' , serif;

$color-1:rgb(36,31,33);
$color-2:rgb(226,1,56) ;
$color-5:rgb(167,169,173);
$color-4:rgb(44,94,164);
$color-3:rgb(108,74,20);
$white:#ddd;
$black:#333;

$borderRadius: 15px;
$shadow: 0 1px 3px #333;
$minHeight: 95vh;

@mixin verticalCenter{
    display:flex;
    justify-content: center;
    align-items: center;
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body{
    margin: 1rem auto;
    max-width: 1200px;
    font-family: $font-1;
    color: $black;
}
button{
    cursor: pointer;
}
li{
    list-style-type: none;
}
a{
    text-decoration:none;
    color: $black;
    cursor: pointer;
}
a:visited{
    color: $black;
}
.home, .knowledges, .portfolio, .contact {
    display: flex;

    @media screen and (max-width:850px){
        display: block !important;
    }
}
.homeContent, .knowledgesContent, .portfolioContent, .contactContent{
    margin-left: .4rem;
    @media screen and (max-width:850px){
        margin-left: 0 !important;
    }
}