.home {

    .homeContent {
    
      min-height: $minHeight;
      width: 100%;
      min-height: 500px;
      border-radius: $borderRadius;
      position: relative;
      overflow: hidden;
  
      &:after {
        content: '';
        position: absolute;
        height: 100%;
        width: inherit;
        background: url('../../media/acceuil.jpg') no-repeat center/cover;
        z-index: -1;
        filter: brightness(60%);
      }
  
      .content {
          
       width: 100%;
        position: absolute;
        top: 20%;
        left: 5%;
        padding-right: 7%;
        font-family: $font-1;
        
        .pdf {
          a {
            position: absolute;
            margin-top:40%;
            margin-left: 25%;
            font-size: 1.5rem;
            background: $color-3;
            padding: 1rem 2rem;
            border-radius: $borderRadius;
            box-shadow: 0 6px #4b3003;
            transition: 0.2s ease;
  
            &:hover {
              box-shadow: 0 4px#4b3003;
              top: 2px;
              
              &:active {
                  box-shadow: 0 0 x#4b3003;
                  top: 6px;
              }
            }
          }
        }
        h1 {
            
          font-size: 2.8rem;
          font-family: $font-2;
          color: rgb(7, 0, 0);
          
        }
        
      }
    }
  }
  @media screen and (max-width: 850px){
    .homeContent {
      margin: 0 !important;
      .pdf{
        a{
          font-size:1.2rem !important;
        }
      }
      h1 {
          font-size: 2rem !important;
        }
    }

  }
  @media screen and (max-width:580px){
    .homeContent {
      &:after {
      background: url('../../media/acceuil.jpg') no-repeat 60% 50%/cover !important;
      }
      h1 {
        font-size: 1.5rem !important;
      }
      .pdf{
        a{
          font-size:.9rem !important;
        }
      }
    }
  }